.html {
  font-size: 10px;
  height: 100%;
  font-family: IBM Plex Sans;
}
.body {
  height: 100%;
  display: flex;
  flex-direction: column;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.show {
  display: block !important;
}
.hidden {
  display: none !important;
}
.show__flex {
  display: flex;
}
.show__opacity {
  opacity: 1;
  visibility: visible;
  transition: all 1s ease 0s;
}
.hidden__opacity {
  opacity: 0;
  visibility: hidden;
  transition: all 1s ease 0s;
}
.h1 {
  font-size: 2.8rem;
  line-height: 3.6rem;
}
@media screen and (max-width: 1239px) {
  .h1 {
    font-size: 2.4rem;
    line-height: 3.1rem;
  }
}
@media screen and (max-width: 767px) {
  .h1 {
    font-size: 2rem;
    line-height: 2.6rem;
  }
}
.h2 {
  font-size: 2.4rem;
  line-height: 3.1rem;
}
@media screen and (max-width: 1239px) {
  .h2 {
    font-size: 2rem;
    line-height: 2.6rem;
  }
}
@media screen and (max-width: 767px) {
  .h2 {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}
.h3 {
  font-size: 2rem;
  line-height: 2.6rem;
}
@media screen and (max-width: 1239px) {
  .h3 {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}
@media screen and (max-width: 767px) {
  .h3 {
    font-size: 1.6rem;
    line-height: 2.1rem;
  }
}
.h4 {
  font-size: 1.6rem;
  line-height: 2.1rem;
}
@media screen and (max-width: 767px) {
  .h4 {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}
.text {
  font-size: 1.4rem;
  line-height: 1.8rem;
}
@media screen and (max-width: 768px) {
  .text {
    font-size: 1.2rem;
    line-height: 1.6rem;
  }
}
.caption {
  font-size: 1.2rem;
  line-height: 1.6rem;
}
@media screen and (max-width: 767px) {
  .caption {
    font-size: 1rem;
    line-height: 1.3rem;
  }
}
.header__wrapper--menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header__wrapper--menu {
    display: none;
  }
}
.header__wrapper--mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .header__wrapper--mobile {
    display: block;
  }
}
.header__wrapper {
  padding: 24px 0 24px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header__wrapper {
    flex-wrap: wrap;
    padding: 12px 0 22px 14px;
  }
}
.header__search-form {
  width: 53%;
}
@media screen and (max-width: 1239px) {
  .header__search-form {
    width: 48%;
  }
}
@media screen and (max-width: 767px) {
  .header__search-form {
    order: 3;
    width: 100%;
    margin-top: 24px;
    margin-right: 14px;
  }
}
.header__links {
  width: 17%;
  color: #f9fcff;
  font-size: 1.5rem;
  line-height: 1.9rem;
  padding-right: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
}
@media screen and (max-width: 1239px) {
  .header__links {
    font-size: 1.2rem;
    line-height: 1.6rem;
    width: 159px;
  }
}
.header__links-circle {
  border-radius: 100%;
  background: #5f688b;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px 12px 7px 12px;
}
@media screen and (max-width: 1239px) {
  .header__links-circle {
    padding: 5px 9px 5px 9px;
  }
}
.header__links-arrow {
  width: 10px;
  height: 20px;
}
@media screen and (max-width: 1239px) {
  .header__links-arrow {
    width: 8px;
    height: 16px;
  }
}
@media screen and (max-width: 1239px) {
  .header__links-img {
    height: 40px;
    width: 22px;
  }
}
.header {
  background: #1d233a;
  display: flex;
  flex-direction: column;
}
.logo {
  width: 183px;
  height: 70px;
}
@media screen and (max-width: 1239px) {
  .logo {
    width: 157px;
    height: 60px;
  }
}
@media screen and (max-width: 767px) {
  .logo {
    width: 105px;
    height: 40px;
  }
}
.wrapper {
  margin: 0 auto;
  max-width: 1240px;
  width: 100%;
}
.search-form__input {
  width: 100%;
  height: 54px;
  padding: 16px 24px;
  box-sizing: border-box;
  background: #f9fcff;
  opacity: 0.8;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-form__input:focus {
  outline: 0;
  border: none;
  opacity: 1;
}
@media screen and (max-width: 1239px) {
  .search-form__input {
    height: 40px;
  }
}
.search-form__button {
  width: 58px;
  height: 54px;
  position: absolute;
  top: 0;
  right: 0;
}
.search-form__button:hover .search-form__img {
  fill: #0e0b0b;
}
@media screen and (max-width: 1239px) {
  .search-form__button {
    width: 40px;
    height: 40px;
  }
}
.search-form__img {
  width: 20px;
  height: 20px;
  fill: #f9fcff;
}
@media screen and (max-width: 1239px) {
  .search-form__img {
    width: 15px;
    height: 16px;
  }
}
.search-form {
  position: relative;
}
.button {
  background: #2d344d;
  border: none;
  font-family: IBM Plex Sans;
}
.button:focus {
  outline: 0;
  outline-offset: 0;
  border: none;
}
.button:hover {
  cursor: pointer;
}
.button--active:hover {
  background: #dae0f3;
  color: #1d233a;
}
.menu__block {
  font-size: 1.5rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  color: #f9fcff;
  padding: 32px 0;
  text-align: center;
  position: relative;
}
.menu__block:first-child {
  padding-left: 32px;
}
.menu__block:last-child {
  padding-right: 32px;
}
.menu__block:hover {
  color: #96b2ed;
  cursor: pointer;
}
.menu__block:hover .menu__hidden-box {
  display: block;
}
@media screen and (max-width: 1239px) {
  .menu__block {
    font-size: 1rem;
    line-height: 1.3rem;
    text-align: left;
    padding: 16px 30px;
  }
  .menu__block:first-child {
    padding-left: 32px;
  }
  .menu__block:last-child {
    padding-right: 32px;
  }
}
.menu__wrapper {
  padding: 24px;
  color: #4d4d4d;
  border-radius: 0 0 2px 2px;
  background: #f9fcff;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.menu__list--mibile {
  max-width: 100%;
  margin-bottom: 0;
  text-transform: uppercase;
}
.menu__list {
  text-align: left;
  width: max-content;
  list-style: none;
  margin-bottom: 16px;
  max-width: 290px;
}
.menu__list:last-child {
  margin-bottom: 0;
}
.menu__list-title {
  display: flex;
  align-items: baseline;
  margin-bottom: 4px;
  padding-left: 11px;
  position: relative;
  font-size: 1.4rem;
  line-height: 1.8rem;
}
.menu__list-title::before {
  position: absolute;
  top: 6px;
  left: 0;
  content: "";
  width: 6px;
  height: 6px;
  background: #5f688b;
  margin-right: 4px;
  vertical-align: baseline;
}
.menu__list-title:hover {
  color: #96b2ed;
}
.menu__item {
  margin-left: 24px;
  text-transform: none;
  margin-bottom: 2px;
  font-size: 1.6rem;
  line-height: 2.1rem;
}
.menu__item:hover {
  color: #96b2ed;
}
@media screen and (max-width: 767px) {
  .menu__item {
    font-size: 1.6rem;
    line-height: 2.1rem;
  }
}
.menu__item--mobile {
  margin-bottom: 5px;
}
.menu__hidden-box {
  position: absolute;
  top: 83px;
  display: none;
}
@media screen and (max-width: 1239px) {
  .menu__hidden-box {
    top: 45px;
  }
}
@media screen and (max-width: 1016px) {
  .menu__hidden-box {
    top: 58px;
  }
}
.menu__btn {
  width: 100%;
  height: 36px;
  text-align: left;
  padding: 13px 14px;
  color: #f9fcff;
  text-transform: uppercase;
  font-size: 1.5rem;
  line-height: 1.9rem;
  display: flex;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background: #464e6a;
}
@media screen and (max-width: 676px) {
  .menu__btn {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}
.menu__img--active {
  transform: rotate(-90deg) !important;
}
.menu__img {
  width: 5px;
  height: 10px;
  transform: rotate(90deg);
  margin-left: 12px;
  position: absolute;
  right: -15px;
  top: 4px;
}
.menu__mobile-block {
  background: #f9fcff;
  color: #4d4d4d;
  padding: 10px 14px 10px 14px;
  font-size: 1.4rem;
  line-height: 1.8rem;
}
.menu__mobile-block:last-child {
  border-bottom: 2px solid #464e6a;
}
a {
  text-decoration: none;
  color: #4d4d4d;
}
.menu__btn-text {
  position: relative;
}
.menu {
  background: #464e6a;
  color: #f9fcff;
}
.footer__wrapper--info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 24px;
  color: #f9fcff;
}
@media screen and (max-width: 767px) {
  .footer__wrapper--info {
    flex-wrap: wrap;
    padding: 16px 14px;
  }
}
.footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 36px 32px;
}
@media screen and (max-width: 1239px) {
  .footer__wrapper {
    overflow: hidden;
    padding: 30px 32px;
  }
}
.footer__all-size {
  background: #1d233a;
}
.footer__content-box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 70%;
  opacity: 0.8;
}
@media screen and (max-width: 767px) {
  .footer__content-box {
    flex-direction: column;
    align-items: flex-start;
    width: auto;
    width: 90%;
  }
}
.footer__information {
  font-size: 1.2rem;
  line-height: 1.6rem;
}
@media screen and (max-width: 1239px) {
  .footer__information {
    flex-direction: column;
    width: 50%;
  }
}
@media screen and (max-width: 767px) {
  .footer__information {
    width: auto;
  }
  .footer__information:last-child {
    flex-direction: initial;
  }
  .footer__information:last-child > .footer__description {
    margin-bottom: 0;
  }
}
.footer__description {
  padding-right: 24px;
}
@media screen and (max-width: 1239px) {
  .footer__description {
    margin-bottom: 12px;
  }
  .footer__description:last-child {
    margin-bottom: 0px;
  }
}
@media screen and (max-width: 767px) {
  .footer__description:last-child {
    margin-bottom: 12px;
  }
}
.footer__link {
  color: #f9fcff;
  text-decoration: underline;
}
.footer__link:hover {
  color: #96b2ed;
}
.footer__logo {
  width: 157px;
  height: 60px;
  opacity: 0.8;
}
@media screen and (min-width: 768px) and (max-width: 1239px) {
  .footer__logo {
    width: 146px;
    height: 56px;
    margin-right: 74px;
  }
}
@media screen and (max-width: 767px) {
  .footer__logo {
    width: 105px;
    height: 40px;
    margin-bottom: 12px;
  }
}
@media screen and (max-width: 1239px) {
  .footer__acc {
    height: 76px;
    justify-content: flex-end;
  }
}
@media screen and (max-width: 767px) {
  .footer__acc {
    height: 120px;
  }
}
.footer__banner {
