.footer {
  display: flex;
  flex-direction: column;
}
.acc__link {
  padding-bottom: 8px;
}
.acc__link:last-child {
  padding-bottom: 0;
}
.acc {
  display: flex;
  flex-direction: column;
}
.main {
  flex-grow: 1;
}
.popup__body {
  width: 389px;
  max-height: 580px;
  overflow-y: auto;
  background: #f9fcff;
