  align-items: center;
  text-transform: uppercase;
  width: 193px;
  color: #ffffff;
}
.popup__btn-img {
  margin-right: 24px;
}
.popup__list {
  margin-top: 32px;
  list-style: none;
  font-size: 1.5rem;
  line-height: 1.9rem;
  text-transform: uppercase;
}
.popup__list--untop {
  margin-top: 0;
}
.popup__list-item {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
