}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(14, 11, 11, 0.6);
  top: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}
.information {
  display: flex;
  color: #f9fcff;
}
